/* GLOBAL CSS */
body {
  margin: 0;
  padding: 0%;
  overflow-x: hidden;
}
:root{
  font-family: "Britannic";
}
::placeholder{
  color: rgb(127, 114, 162) ;
}
::selection{
  background: rgba(1, 255, 1, 0.496);
}
a{
  text-decoration: none;
  color: black;
}
.imp{
  color: green;
}


/* NAVBAR CSS */
.container, .container-fluid, .container-lg, .container-md, .container-sm, .container-xl, .container-xxl {
  width: auto;
}
.navbar-brand img{
  width: 120px;
}
.dropdown:hover .dropdown-menu {
  display: block;
}
.dropdown-menu {
  margin-top: 0px;
}
.menu{
  display: flex;
  flex-direction: row;
}



/* HOME PAGE CSS */
.home_section1{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px;
}
.home_section1 img{
  width: 85%;
}
.home_section2{
  width: 80%;
  margin: 20px auto;
}

.home_section3{
  max-width: 1200px;
  width: 80%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 50px auto;
}
.home_section3 .areca_product{
  list-style: none;
  text-decoration: none;
  color: black;
  box-shadow: -15px 15px 40px rgb(140, 140, 140);
  background-color: white;
  width: 30%;
  padding: 20px;
  min-width: 350px;
  margin: 10px 5%;
  display: flex;
  flex-direction: column;
  border-radius: 4%;
  cursor: pointer;
}
.home_section3_title{
  font-size: 20px;
  font-weight: 600;
  border-left: 10px solid green;
  padding: 7px 15px;
  background-color: #f7f7f7;
  width: 100%;
  border-radius: 3px;
  text-shadow: #c5c5c5 1px 1px 1px;
}
.home_section3 img{
  max-width: 200px;
  width: 100%;
  margin: auto;
  border-radius: 5px;

}
.home_section3_leaf_product_name{
  color: white;
  font-size: 20px;
  text-align: center;
  padding: 5px;
  border: 1px solid white;
  margin-top: 15px;
  border-radius: 30px;
  
}





/* FOOTER CSS */
.footer{
  background-color: black;
  display: flex;
  flex-direction: column;
  color: white;
  width: 100vw;
  justify-content: center;
  align-items: center;
}
.footer_sections{
  width: 95%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.footer_section1{
  margin:10px 3.5%;
}
.footer_section1 li:not(:first-child){
  padding: 5px 0;
  list-style:'üçÅ';
}
.footer_section1 li a{
  color: rgb(255, 255, 255);
}
.footer_title{
  list-style: none !important;
  font-size: 18px;
  padding:20px 0 !important;
  font-weight: 600;
}
.copyright{
  color: gray;
  text-align: center;
  padding: 10px;
}
.social_menu{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100vw;
  margin: 10px 0;
  flex-wrap: wrap;
}
.social_menu svg{
  margin: 0 5px;
  width: 45px;
  height: 45px;
  min-width: 20px;
  min-height: 20px;
  cursor: pointer;
  padding: 10px;
}
.social_menu a{
  color: rgb(123, 122, 122);
  transition: 0.5s;
}
.social_menu a:hover{
  color: rgb(0, 255, 0);
  transition: 0.5s;
}
.social_menu a svg:active{
  color: rgb(3, 20, 3);
  transition: 0.5s;
}




/* CERTIFICATE PAGE */
.certificate_section1{
  background-image: url("http://bioworld.in/wp-content/uploads/2021/05/inner-banner.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100vw;
  height: 318px;

}
.certificate_section1_overelay{
  width: 100vw;
  height: 318px;
  background-color: #81B910;
  opacity: .73;
  z-index: 2;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
}
.certificate_section1_text{
  font-size: 38px;
  font-family: "Stylish",Sans-serif;
  font-weight: bold;
  color: white;
}
.certificate_section2{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 90vw;
  margin: 60px auto;
}
.certificate_section2_certificate{
  text-decoration: none;
  color: #81B910;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 5%;
}
.certificate_section2_pdfimg{
  background-color: #216902;
  width: 120px;
  height: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
}
.certificate_section2_pdfimg svg{
  width: 60px;
  height: 60px;
  fill: white;
  min-width: 30px;
  min-height: 30px;
}
.certificate_section2_certificate_name{
  padding: 10px 0;
  font-size: 20px;
  font-weight: bold;
  color: #81B910;
  font-family: "Stylish",Sans-serif;
}

.certificate_section3{
  background-color: #F2F2F2;
  padding: 25px 15px;
  display: flex;
  flex-direction: column;
  font-family: 16px;
}
.certificate_section3 div{
  display: flex;
}
.certificate_section3 span{
  color:#354e02;
  font-weight: bold;
  font-size: 20px;
  padding-right: 7px;
}



/* SIDE BAR & OTHER JOINER IN SIDE BY SIDE */
.abcd{
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100vw;
  margin: auto;
}



/* ARECA PLATES */
.areca_plates{
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 30px 0;
  max-width: 1000px;
  width: 75vw;
}
.areca_plates_main_title{
  text-align: center;
  font-size: 35px;
  font-weight: 600;
  color:#cea08a;
  margin-bottom: 20px;
}
.areca_plates_top_title_border{
  width: 100%;
  height: 10px;
  border-bottom:1px solid #cea08a;
  padding-bottom: 5px;
  border-top: 7px solid #cea08a;
}
.areca_plates_sub_title{
  font-size: 24px;
  color:#cea08a;
  text-align: center;
  text-decoration: underline;
  margin-top: 15px;
}
.areca_plates_types{
  flex-wrap: wrap;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: auto;
}
.areca_plates_type{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 20px 5%;
}
.areca_plates_type_image{
  width: 85%;
}
.areca_plates_type_image img{
  width: 100%;
}
.areca_plates_type_name{
  text-align: center;
  font-size: 16px;
  color: #000000;
  margin-top: 10px;
}
.areca_plates_type_more{
  color: #000000;
}



/* FRORK KNIFE SPOON PAGE */
.frork_knife_spoon_image_name{
  width: 80%;
}
.frork_knife_spoon_image_name img{
  width: 100%;
}
.frork_knife_spoon_name{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 75%;
  text-align: center;
  margin: auto;
  font-size: 16px;
  color: #000000;
  margin-top: 10px;
}




/* SIDEBAR */
.side_bar{
  width: 280px;
  margin: 30px 0;
  border: 1px solid #f7f7f7;
  padding: 0 15px;
}
.side_bar ul{
  list-style: none;
}
.side_bar_main_li{
  font-size: 16px !important;
  font-weight: 600;
  border-left: 10px solid green;
  padding: 15px 15px !important;
  margin-top: 20px;
  margin-bottom: 5px;
  background-color: #f7f7f7;
}
.side_bar_down_li{
  padding: 10px 5px;
  margin: 0 15px;
  font-size: 14px;
  border-bottom: 1px solid #f7f7f7;
  border-top: 1px solid #f7f7f7;
  list-style: 'üçí';
}




/* CONTACT US PAGE */
.contactus{
  display: flex;
  flex-direction: column;
  margin-bottom: 40px;
}
.contactus_top_image{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.contactus_top_image img{
  width: 50%;
}
.contactus_title_path{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color:#5b8c16;
  color: white;
  padding: 15px;
}
.contactus_path span:nth-child(1){
  color: #D4BF16;
}
.contactus_office_touch{
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: center;
  margin: auto;
}
.contactus_office{
  display: flex;
  flex-direction: column;
  width: 30%;
  margin-top: 40px;
  margin-right: 2%;
}
.contactus_office h4{
  background-color:#5b8c16;
  color: white;
  padding: 15px;
  margin-bottom: 30px;
  border-radius: 5px;
}
.office_details{
  display: flex;
  flex-direction: column;
}
.office_address{
  display: flex;
  flex-direction: column;
  background-color: #FAE7E8;
  border-left: 6px solid #e45460;
  padding: 17px;
  margin: 5px 0;
  border-radius: 5px;
  font-size: 14px;
  box-shadow: 0 1px 6px rgb(0 0 0 / 18%);
}
.office_address_title{
  font-weight: bold;
  margin-bottom:7px;
  text-shadow: #c5c5c5 1px 1px 1px;
}
.office_address_address{
  text-shadow: #c5c5c5 1px 1px 1px;
}
.contactus_touch{
  display: flex;
  flex-direction: column;
  width: 60%;
  max-width: 700px;
  margin-top: 40px;
}
.contactus_touch h4{
  background-color:#5b8c16;
  color: white;
  padding: 15px;
  margin-bottom: 30px;
  border-radius: 5px;
  width: 100%;
}
.form{
  width: 100%;
  display: flex;
  flex-direction: column;
}
.form_input,.form_textarea{
  width: 100%;
  padding: 7px 15px;
  border: 1px solid #ccc;
  color: #555;
  font-size: 14px;
  outline: none;
  border-radius: 5px;
  margin: 7px 0;
}
.form_submit{
  width: 100px;
  border: 1px solid #ccc;
  color: rgb(0, 0, 0);
  font-size: 14px;
  outline: none;
  border-radius: 5px;
  margin: 7px 0;
  padding: 7px 0;
  background-color: white
  ;
}




/* GALLERY CSS */
.gallery{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #517d14;
  color: white;
  max-width: 1200px;
  border-radius: 2%;
  margin: 40px auto;
}
.gallery h1{
  font-weight: bold;
  padding: 15px;
  text-align: center;
}
.gallery_images{
  display: flex;
  flex-wrap: wrap;
  width: 95%;
  justify-content: center;
  align-items: center;
}
.gallery_image img{
  width: 200px;
  height: 200px;
  min-width: 10%;
  margin: 15px;
  border-radius: 5px;
}



/* DUPLES & ABOUT CSS */
.duplex, .about{
    margin: auto;
    display: flex;
    flex-direction: column;
    margin: 50px 2%;
    width: 75vw;
    max-width: 850px;
    box-shadow: 0 0 4px 0 rgb(86 86 86 / 50%);
}
.duplex h4, .about h4{
  font-weight: 600;
  border-left: 8px solid #517d14;
  padding: 12px;
  background: #f7f7f7;
}
.duplex h5{
  padding: 15px;
  font-weight: bold;
}
.duplex img:nth-child(2){
  width: 100%;
  margin: 0;
  padding: 0%;
  margin-bottom: 20px;
}
.duplex img, .about img{
  width: 90%;
  max-width: 800px;
  margin: 30px auto;
  padding: 20px;
}
.duplex p{
  font-size: 14px;
  margin: 5px 0;
  padding: 5px 15px;
  font-weight: 600;
  line-height: 1.1;
}
.duplex .table{
  margin: 20px 0;
}
.duplex thead{
  background-color: rgb(0, 157, 255);
  color: white;
  text-align: center;
}
.duplex tr{
  vertical-align: middle;
}
.duplex thead th{
  font-size: 15px;
}
.duplex tbody{
  font-size: 14px;
  border: 1px solid rgb(0, 0, 0);
}
.duplex td{
  border-right: 0.5px solid rgb(0, 0, 0);
}
.about p{
  font-size: 16px;
  margin: 10px 0;
  padding: 15px;
  font-weight: 600;
  line-height: 1.1;
}
.about img{
  width: 80%;
  max-width: 350px;
}
.about_top_image{
  display: flex;
  justify-content: center;
  align-items: center;
}
.about_top_image img{
  width: 90%;
  max-width: 1100px;
}



/* MGCRAFT CSS */
#mgcraft{
  margin: 50px auto;
  width: 65vw;
  max-width: 850px;
}
.mg_craft img{
  margin: auto;
  width: 100%;
  margin-bottom: 20px;
}
.mg_craft p{
  font-size: 16px;
  margin: 10px 0;
  padding: 15px;
  font-weight: 500;
  line-height: 1.1;
}
.mg_craft span{
  color:#C68F00;
}
.mg_craft h6{
  font-weight: bold;
  color: #990000;
  margin: 10px 0;
  padding: 15px;
  font-size: 18px;
}
.content_box{
  width: 95%;
  margin: auto;
  box-shadow: 0 0 4px 0 rgb(86 86 86 / 50%);
}
.content_box h2 {
  font-size: 1.5em;
  border-left: 8px solid #517d14;
  font-weight: 600;
  background-color: #f7f7f7;
  padding: 12px;
  width: 100%;
}
.table {
  width: 100%;
  max-width: 100%;
  margin-bottom: 20px;
}








/* ALL MEDIA QUERIES */
@media screen and (max-width: 900px) {
  .contactus_office_touch{
    flex-direction: column;
    align-items: center;
    margin: auto;
  }
  .contactus_office{
    width: 90%;
    max-width: 600px;
  }
  .contactus_touch{
    width: 90%;
    max-width: 600px;
  }
  #mgcraft{
    margin: 50px auto;
    width: 60vw;
    max-width: 850px;
  }
  .footer_section1{
    margin:10px 1%;
  }
  .footer_title{
    font-size: 16px !important;
  }
  .footer_section1 li a{
    font-size: 14px;
  }
  .footer_section1 li:not(:first-child){
    font-size: 14px;
  }
}
@media screen and (max-width: 700px) {

  #mgcraft{
    margin: 50px auto;
    width: 100vw;
    max-width: 850px;
  }
  .side_bar{
    display: none;
  }
  .about{
    width: 95vw;
  }
  .footer_sections{
    width: 95%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }


}
@media screen and (max-width: 602px) {
  .home_section1 img{
    width: 100%;
  }
  .home_section2 h3{
    text-align: center;
    margin-top: 50px;
  }
  .duplex{
    width: 95vw;
}
.duplex img{
  width: 100%;
}
  .footer_section1{
    margin:10px 60px;
  }
  .footer_section1 li:not(:first-child){
    padding: 2px 0;
  }
  .footer_section1 ul{
    padding: 0;
  }
  .footer_title{
    text-align: center;
  }
}

@media screen and (max-width: 467px) {
  .footer_section1{
    margin:10px 20px;
    color: white;
  }
  .certificate_section1_text{
    font-size: 150%;
  }
  .certificate_section2_certificate{
    margin: 10px 5%;
  }
  .certificate_section2_pdfimg{
    width: 100px;
    height: 100px;
  }
  .certificate_section2_pdfimg svg{
    width: 50px;
    height: 50px;
  }
  .certificate_section2_certificate_name{
    font-size: 17px;
  }

  .mrcraft-table .table th{
    font-size: 12px;
  }
  .mrcraft-table .table td{
    font-size: 12px;
  }
  .home_section3 .areca_product{
    list-style: none;
    text-decoration: none;
    color: black;
    box-shadow: -15px 15px 40px rgb(140, 140, 140);
    background-color: white;
    width: 30%;
    padding: 20px;
    min-width: 80vw;
    margin: 10px 5%;
    display: flex;
    flex-direction: column;
    border-radius: 4%;
    cursor: pointer;
  }

  .about h4{
    font-size: 18px;
  }
  .about p{
    font-size: 14px;
  }
}
@media screen and (max-width: 1200px) {
  .header{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 0;
    margin: 0 20px;
  }

  .navbar_box{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: auto;
    width: 90vw;
  }
  .footer_sections{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    color: white;
  }
}
@media screen and (max-width: 1330px) {
  .home_section3{
    width: 90%;
  }
  .home_section2{
    width: 90%;
  }
}